@charset "utf-8";

@import "_variables.scss";

.approximate-color-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: calc(100% / 41);
    height: 96px;
    padding: 4px 0;
    border: 1px dotted $light-gray;
    font-size: 1.0rem;

    &--white {
        color: $white;
    }

    &--black {
        color: $black;
    }
}
